
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  overflow: hidden;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.white {
  color: #fff;
}
.black {
  color: #4e4e4e;
}

#view[data-v-7b00f5fe] {
  background-color: #193c6d;
  filter: progid: DXImageTransform.Microsoft.gradient(gradientType=1, startColorstr="#003073", endColorstr="#029797");
  background-size: 100%;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#003073), to(#029797));
  background-image: linear-gradient(0deg, #003073, #029797);
  text-align: center;
  margin: 0px;
  overflow: hidden;
}
#vr[data-v-7b00f5fe] {
  position: absolute;
  top: 7px;
  right: 11px;
  cursor: pointer;
}
#fullscreen[data-v-7b00f5fe] {
  position: absolute;
  top: 5px;
  right: 13px;
  cursor: pointer;
}
#play[data-v-7b00f5fe] {
  position: absolute;
  top: 5px;
  left: 13px;
  cursor: pointer;
}


.fa-icon {
  display: inline-block;
  fill: currentColor;
}
.fa-flip-horizontal {
  -webkit-transform: scale(-1, 1);
          transform: scale(-1, 1);
}
.fa-flip-vertical {
  -webkit-transform: scale(1, -1);
          transform: scale(1, -1);
}
.fa-spin {
  -webkit-animation: fa-spin 1s 0s infinite linear;
          animation: fa-spin 1s 0s infinite linear;
}
.fa-inverse {
  color: #fff;
}
.fa-pulse {
  -webkit-animation: fa-spin 1s infinite steps(8);
          animation: fa-spin 1s infinite steps(8);
}
@-webkit-keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes fa-spin {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
